</template>
<script>
import DataService from "@/services/DataService";
export default {
    components: {

    },
    data() {
        return {
            isLoading: false,
            ApplicantName: '--',
            ApplicantId: 0,
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            Fromtype: '',
            TotalRecords: 0,
            Headers: null,
            CandidateData: [],
            CandidateDetail: [],
            documentList: [],
            CurrentPage: 1,
            TotalPages: 0,
            noRecord: false,
            candidate_profile_path: "~/Content/images/DefaultImages/avatar-icon.jpg",
        }
    },
    created: async function () {
        this.userId = this.GetUserInfo.ID;
        this.companyId = this.GetUserInfo.CompanyId;
        await this.getForm(this.salesorderID);
        await this.getDocumentList();
    },
    methods: {
        getForm: async function (id) {
            var vm = this;
            vm.ApplicantId = this.$route.params.id;
            vm.Fromtype = this.$route.params.type;
            vm.isLoading = true;
            var url = `applicantId=${vm.ApplicantId}&skillIds=${vm.ApplicantId}&departmentIds=0&langCode=EN`;
            await DataService.TalentPoolDetails(url).then(function (response) {
                if (response != null && response.data != null) {
                    vm.isLoading = false;
                    vm.Headers = response.data.schema;
                    if (response.data.data.length > 0) {
                        vm.CandidateData = response.data.data;
                        if(response.data.data[0].profile_path){    
                            vm.BindProfileSrc(response.data.data[0].profile_path);
                        }
                        vm.CandidateDetail = response.data.data[0];
                        vm.TotalRecords = response.data.data[0].total_records;
                        vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                        vm.noRecord = false;
                    } else {
                        vm.noRecord = true;
                        vm.CandidateData = [];
                        vm.TotalRecords = 0;
                        vm.ShowAlert(vm.$t("No_Record_Found"), "warning", true, vm.$t("Alert"));
                    }
                }
            });
        },
        BindProfileSrc: async function (filepath) {
            var vm = this;
            var dynamicSrc = '';
            var path = filepath.substring(0, filepath.lastIndexOf("/") + 1);
            var fileName = filepath.substring(filepath.lastIndexOf("/") + 1, filepath.length);
            let Obj = {
                path: path,
                fileName: fileName
            };
            var url = JSON.stringify(Obj);
            await DataService.GetFileSrc(url).then((response) => {
                if (response.data != '') {
                    vm.candidate_profile_path = response.data
                }
            });
            return dynamicSrc;
        },
        Close: function () {
            var vm = this;
            if (vm.Fromtype == 'JobListing') {
                this.$router.push({ name: 'JobList' });
            } else {
                this.$router.push({ name: 'TalentPoolList' });
            }
        },
        AddAsCandidate: function () {
            var vm = this;
            vm.$router.push({ name: 'HRAddAsCandidate', params: { id: vm.ApplicantId } });
        },
        DownloadResume: async function (path, name) {
            var vm = this;
            var fileName = name;
            var filePath = path;
            if (filePath == "" && fileName == "" || filePath == null) {
                vm.ShowAlert(vm.$t("NoAttachment"), "warning", true, vm.$t("Alert"));
            } else {
                var url = `filePath=${filePath}&fileName=${fileName}`;
                await DataService.DownloadFile(url).then(async function (response) {
                    const byteData = response.data;
                    const fileURL = `data:image/png;base64;base64,${byteData}`;
                    var fileLink = document.createElement('a');
                    fileLink.href = fileURL;
                    fileLink.setAttribute('download', fileName);
                    document.body.appendChild(fileLink);
                    fileLink.click();
                })
            }
        },
        JobDeatailRedirection:function(value){
            var vm=this;
            vm.$router.push({ name: 'JobPost', params: { id: value, type: 'JobPostDetail' } });
        },
        getDocumentList: async function () {
            var vm = this;
            var url = `applicantId=${vm.ApplicantId}&langCode=EN`;
            await DataService.GetDocumentsList(url).then(function (response) {
                if (response.data != null) {
                    vm.Headers = response.data.schema;
                    if (response.data != "" && response.data.data.length > 0) {
                        vm.documentList = response.data.data;
                    } else {
                        vm.documentList = [];
                    }
                }
                else {
                    vm.documentList = [];
                }
            })
        },
    },
}
</script>